import { Slot } from '@radix-ui/react-slot'
import clsx from 'clsx'
import { ButtonHTMLAttributes, ReactNode } from 'react'

export type ButtonProps = ButtonHTMLAttributes<HTMLButtonElement> & {
  children: ReactNode

  asChild?: boolean
}

export function Button({ children , asChild, className, ...rest }: ButtonProps) {
  const Component = asChild ? Slot : 'button'

 const internalClassName = clsx('font-sans py-3 px-4 bg-cyan-500 rounded font-semibold text-black text-sm w-full transition-colors hover:bg-cyan-300 focus:ring-2 ring-white', className)

  return (
    <Component
      className={internalClassName}
      {...rest}
    >
      {children}
    </Component>
  )
}
